
@import url('https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap');
.brand-logo{
    font-family: 'Grand Hotel', cursive;   
}

.card-img{
    width: 30px;
    border-radius: 30px;
   display: flex;
 margin-left: 10px;
 margin-top:5px;
    object-fit: contain;
   
}
.mycard{
    
    margin: 30px;
}
.auth-card{
    font-family: 'Grand Hotel', cursive;  
    color: black;
    background-color:#092c8db5;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.5); 
    margin:10px auto ;
    border-radius: 60px;
padding: 20px;
text-align: center;
max-width: 400px;
height: 500px;
max-height:400px ;
margin: 10px auto;


}

.Post{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    
  
}
.item{
    width: 30%;

}


.home-card{
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.5); 
   padding: auto;
    border-radius: 20px;
    margin:20px auto;
    max-width:500px ;
    height: max-content;
  
}
#toast-container{
    top:30px !important;
    right: 20px !important;
    left: auto !important;
  }

  *{
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
    text-transform: capitalize;
    font-weight:500;
    font-family: Arial, Helvetica, sans-serif;
  
  
  }
  .material-icons:hover{
    cursor: pointer;
  }
  .input-filled{
    
    
   
    background-color: rgba(113, 34, 187, 0.823);
  }
  body{
    background-image: url("./bg.jpg");
    background-size: cover;
    background-attachment: fixed;
  }
 
@media only screen and (max-width: 600px) {

  .brand-logo{
    padding-left:1px!important;
  }
  .bt{
      margin-right: 2px!important;
      width: 70px!important;
      font-size: 11px;
  }
  .pict{
    width: 70px!important;
    height: 70px!important;
    
  }
  .head>h6{
    font-size: small!important;
    margin: 17px;
  }
  .hedi{


    width: 100%!important;
  }
  hedi>h5{
    margin: 1px;
    font-size: 10px!important;
  }
  }
  a{
    color:"rgb(82, 6, 132)"!important;
  }
  